  background-color: var(--primary-color);
  color: #fff;
  font-size: 20px;
  margin-top: 50px;
  padding: 15px 32px;
  border-radius: 4px;
  cursor: pointer;
}

.rush-button:hover {
  background-color: var(--primary-dark);
  color: #fff;
}

/* Table Row */
@media (min-width: 992px) {
  .table-row {
    border-top: 4px solid;
    border-bottom: 10px solid;
    border-radius: 13px;
  }

  .table-row > [class*='col-'] {
    float: none;
    padding: 10px 30px;
  }
  .table-row > [class*='col-']:first-child {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 4px;
  }
  .table-row > [class*='col-']:last-child {
    border-top-right-radius: 10px;
    border-bottom-right-radius: 4px;
  }

  .table-row > [class*='col-'].odd {
    background-color: var(--accent-light);
    color: var(--secondary-color);
  }
  .table-row > [class*='col-'].even {
    background-color: var(--primary-color);
    color: var(--accent-light);
  }
}

/* Rush Steps */
@media (max-width: 991px) {
  .table-row > [class*='col-']:first-child .rush-steps {
    border-top: 4px solid var(--secondary-color);
    border-radius: 13px 13px 0 0;
  }
  .table-row > [class*='col-']:last-child .rush-steps {
    border-bottom: 10px solid var(--secondary-color);
    border-radius: 0 0 13px 13px;
  }

  .rush-steps {
    padding: 40px 20px;
  }

  .rush-steps.odd {
    background-color: var(--accent-light);
    color: var(--secondary-color);
  }
  .rush-steps.even {
    background-color: var(--primary-color);
    color: var(--accent-light);
  }
}

/* Step Triangle Arrow */
.step-triangle:after {
  content: '';
  display: block;
  position: absolute;
  z-index: 1;
}
@media (min-width: 992px) {
  .step-triangle:after {
    top: 48%;
    right: -11.5%;
    width: 32px;
    height: 32px;
    -webkit-transform: rotate(-45deg) translateY(-50%);
    transform: rotate(-45deg) translateY(-50%);
  }
}
@media (min-width: 1200px) {
  .step-triangle:after {
    width: 40px;
    height: 40px;
  }
}
@media (max-width: 992px) {
  .step-triangle:after {
    bottom: -3%;
    left: 49.2%;
    width: 40px;
    height: 40px;
    -webkit-transform: rotate(-45deg) translateY(-50%);
    transform: rotate(-45deg) translateX(-50%);
  }
}
@media (max-width: 768px) {
  .step-triangle:after {
    bottom: -2%;
    left: 48.5%;
  }
}

.step-triangle.odd:after {
  background-color: var(--accent-light);
}
.step-triangle.even:after {
  background-color: var(--primary-color);
}

.table-row > [class*='col-']:last-child .step-triangle {
  display: none;
}

.about-description {
  display: flex;
  justify-content: center;
  font-size: 30px;
  text-align: left;
}
@media (min-width: 992px) {
